.block__2 {
  
  background: #8A94C9;
  background-image: url('../img/background-block-2.png');
    background-repeat: no-repeat;
    background-attachment: center;
    background-size: cover;
    background-attachment:fixed;
    background-position: center;
  .header__info {
    position: relative;
    .image__present {
      position: absolute;
      width: d-font(30, 115, $v-min, $v-max);
      height: d-font(40, 152, $v-min, $v-max);
      
      left: d-font(25, 250, $v-min, $v-max);
      top: 10%;
      z-index: 300;
      img {
        width: 100%;
        height: auto;
      }
    }
    .header__block_2 {
      position: relative;
      margin: 0;
      height: 40px;
      background-color: #6959B6;
      
    .header__text {
        position: absolute;
        text-align: center;
        width: d-font(195, 628, $v-min, $v-max);
        max-width: 628px;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        p {
          margin: 0;
          padding: 0;
          font-style: normal;
          font-weight: normal;
          font-size: d-font(8, 24, $v-min, $v-max);
          line-height: 150%;
          
          text-transform: uppercase;
          color: #FFFFFF;
        }
      }
    }
   .image__header {
      width: 100%;
      img {
        width: 100%;
      }
    } 
  }
  .title__block_2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 34px;
      h3 {
        width: 80%;
        font-style: normal;
        font-weight: bold;
        font-size: d-font(18, 48, $v-min, $v-max);
        line-height: 110%;
        text-align: center;
        text-transform: uppercase;
        color: #392D71;
      }
    
  }
  .description__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    .content__block_2 {
      width: 80%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .flex__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        .item__content {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 100px;
          .image_item {
                        width: 140px;
                        img {
                          width: 100%;
                        }
                      }

          .description__item {
            display: flex;
            align-items: center;
            text-align: center;
            
            
            p {
              width: d-font(220, 380, $v-min, $v-max);
              font-style: normal;
              font-weight: normal;
              font-size: d-font(18, 24, $v-min, $v-max);
              line-height: 111%;
              letter-spacing: -0.015em;
              color: #FFFFFF;
              margin-top: px-to-rem(15px);
              margin-bottom: px-to-rem(15px);
            }
          }
        }
      }
    }
    
  }
}
@media screen and (min-width: 534px) { 
    .block__2 {
    
  }
    .item__content {
          display: flex;
          flex-direction: row !important;
          align-items: center;
          .image_item {
              width: 140px;
              img {
                width: 100%;

              }
            }

          .description__item {
            display: flex;
            align-items: center;
            text-align: left !important;
            margin-left: px-to-rem(57px);
            
            p {
              width: d-font(220, 380, $v-min, $v-max);
              font-style: normal;
              font-weight: normal;
              font-size: d-font(18, 24, $v-min, $v-max);
              line-height: 111%;
              letter-spacing: -0.015em;
              color: #000000 !important;
              margin-top: px-to-rem(15px);
              margin-bottom: px-to-rem(15px);
            }
          }
        }
}
@media screen and (min-width: 1280px) {
  
  .description__block {
    .content__block_2 {
      flex-direction: row !important;
      align-items: flex-start !important;
      justify-content: space-around;
      margin-bottom: px-to-rem(100px);
      .flex__content {
        justify-content: space-between !important;
        .item__content {
          min-height: 160px;
          margin: 0 0 50px 0 !important;

          img {
            height: 100% !important;
            width: auto !important;
          }
        }
      }
    }
  }
}