.button__block {
	display: none;
}
.header {
	height: 100%;
	width: 100%;
	box-sizing: border-box;
	background-image: url('../img/background_block_header.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: bottom center;
	background-attachment:fixed;
	.container__box {
		width: 80%;
		height: 100%;
		min-height: 100%;
		display: flex;
		flex-direction: column;
		.title__header{
			display: flex;
			flex-direction: row;
			align-items: center;
			padding-top: 10px;
			padding-bottom: 10px;
			justify-content: space-between;
			h3 {
					margin-right: d-font(0, 55, $v-min, $v-max);
					font-style: normal;
					font-weight: 800;
					font-size: d-font(14, 48, $v-min, $v-max);
					line-height: 110%;
					display: flex;
					align-items: center;
					text-align: center;
					color: #FFFFFF;
					text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
				}
				p {
					background-image: url('../img/icon_phone.svg');
					background-repeat: no-repeat;
					background-size: contain;
					margin: 0;
					padding-left: d-img(20, 35, $v-min, $v-max);
					font-style: normal;
					font-weight: 500;
					font-size: d-font(12, 24, $v-min, $v-max);
					line-height: 130%;
					display: flex;
					align-items: center;
					text-align: center;
					color: #FFFFFF;
				}
			.logo__title {
				width: d-img(132, 380, $v-min, $v-max);
				display: flex;
				flex-direction: column;
				align-items: start;
				margin-left: d-img(0, 48, $v-min, $v-max);
				img {
					width: 100%;
				}
			}
			}
			.btn {
				width: 192px;
				height: 30px;
				background: #EEEEF0;
				box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
				border: none;
				border-radius: 5px;
				font-size: 16px;
				color: #393185;
			}
		}
		.content {
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			.content__header {
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				width: 100%;
				// width: d-img(258, 400, $v-min, $v-max);
				img {
						width: 100%;
					}
				.sub__header {
					width: 100%;
					display: flex;
					flex-direction: column;
					justify-content: center;
					font-style: normal;
					font-weight: bold;
					font-size: d-font(12, 18, $v-min, $v-max);
					line-height: 130%;
					display: flex;
					align-items: center;
					text-align: center;
					color: #FFFFFF;
					text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
				}
			}
			
			.couch__header {
				
				flex: 1;
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				align-items: center;
				width: 100%;
				img {
					// width: d-img(200, 600, $v-min, $v-max);
					width: 	100%;
				}
			}
			
		}
		.button__header {
			width: 100%;
			height: 70px;
			background-color: white;
			font-style: normal;
			font-weight: 900;
			font-size: 13px;
			line-height: 10px;
			display: flex;
			flex-direction: row;
			letter-spacing: 0.04em;
			text-transform: uppercase;
			justify-content: 	center;
			align-items: center;
			a{		
				display: flex;
				align-items: center;
				text-align: center;
				justify-content: center;	
				text-decoration: 	none;
				border-radius: 6px;
				padding-top: 	px-to-rem(8px);
				padding-bottom: px-to-rem(8px);
				height: 	52px;
				
				color: white;
				background: #933294;
				border: 2px solid #380359;
				box-sizing: border-box;
				width: 	80%;
				
			}
			
		}
}
// Form__header
.form__header {
		display: none;
}
.content__header {
	button {display: none;}
}
