@media screen and (min-width: 1000px) {
	.title__header {
		// justify-content: space-around !important;
		.button__block {
			margin-left: d-img(10, 20, $v-min, $v-max);
			display: flex;
			align-items: center;
		}
	}
}
// Контент CONTENT
@media screen and (min-width: 534px) {
		.header {
			.button__header {
				display: none;
			}
		}
		.content {
			display: flex;
			flex-direction: row !important;
			.content__header {
				align-self: stretch;
				justify-content: center !important;
				max-width: 581px;
				
				img {
					
					margin-bottom: 2rem;
					width: 100%;
					height: 	auto;
				}
				button {
					display: block;
					font-size: d-font(10, 18, $v-min, $v-max) !important;
					max-width: d-font(170, 210, $v-min, $v-max);
				}
				#hidden {
					border: 2px solid white;
				}
				.btn {
				   color: white;
				   background: #A52A9C;
				   border-radius: 7px;
				   height: 40px;
				   width: 80%;
				   margin: 0;
				   margin-top: px-to-rem(5px);
				   margin-bottom: px-to-rem(20px);
				   text-transform: none;
				   padding: 0;

				}
				.sub__header {
					margin-bottom: 1rem;
				}
			}
			.couch__header {
				justify-content: stretch;
				align-self: stretch;
				align-items: flex-end !important;
				img {
					width: 100%;
				}
			}

			
		}
	}
@media screen and (min-width: 1500px) {
	.content__header {
		#hidden {
			display: none;
		}
		button {
			border: none !important;
		}
		img {
					margin-bottom: d-font(0, 55, $v-min, $v-max) !important;
				}
		.sub__header {
					margin-bottom: 0 !important;
					width: 70% !important;
				}
	}
	.form__header {
				display: flex;
				flex-direction: column;
				align-items: stretch;
				width: 80%;
				max-height: 321px;
				max-width: 422px;
				margin-bottom: 2rem;
				background: #E1E1E1;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				border-radius: 8px;
				padding: 13px 34px;
				color: #808080;
				font-weight: normal;
				font-size: 17px;
				line-height: 20px;
				letter-spacing: -0.015em;
				text-transform: uppercase;
				input {
				   box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
				   height: 40px;
				   margin: 0;
				   margin-bottom: px-to-rem(10px);
				   width: 100%;
				}
				.form-control {
					background-color: white;
					border-radius: 7px;
					border: none;
				}
				
				p {
					font-style: normal;
					font-weight: 500;
					font-size: px-to-rem(15px);
					line-height: px-to-rem(17px);
					display: flex;
					align-items: center;
					justify-content: center;
					letter-spacing: -0.015em;
					text-transform: uppercase;
					color: #808080;
					margin-top: 0;
					margin-bottom: px-to-rem(8.85px);
				}
				button {
					min-width: 100%;
				}
				p:last-child{
				   margin: 0.5em 0;
				   font-size: 12px;
				   line-height: 12px;
				   text-transform: none;
				   width: 100%;
				   text-align: center;
				}
			}
}

