.block__5 {
	.header__block_5 {
		width: 100%;
		height: d-font(237, 727, $v-min, $v-max);
		background-image: url('../img/childrens.png');
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}
	.bonus__block_5 {
		width: 100%;
		background-color: #7A75B0;
		padding: px-to-rem(10px) 0 px-to-rem(10px) 0;
		.container__box {
			display: flex;
			justify-content: space-around;
			align-items: center;
		}
		.img__block_5 {
			width: d-img(70,156,$v-min, $v-max);
			img {
				width: d-img(70,156,$v-min, $v-max);
			}
		}
		.description__block_5 {
			display: flex;
			flex-direction: column;
			margin-left: d-font(20, 100, $v-min, $v-max);
			font-style: normal;
			font-weight: 500;
			font-size: d-font(8, 24, $v-min, $v-max);
			line-height: 135%;
			letter-spacing: -0.015em;
			text-transform: uppercase;
			color: #FFFFFF;
			p {margin: 0;}
		}
	}
}
@media screen and (min-width: 814px) {
	.block__5 {
	.header__block_5 {
		width: 100%;
		height: d-font(237, 727, $v-min, $v-max);
		background-image: url('../img/childrens_2.png');
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}

}
}