.block__6 {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	background: linear-gradient(180.15deg, rgba(196, 191, 191, 0) 49.28%, #C7C6C6 101.04%);
	.content__block_6 {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.title__block_6 {
		width: 100%;
		padding: px-to-rem(20px) px-to-rem(45px);
		display: flex;
		justify-content: center;
		text-align: center;
		h3 {
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18, 36, $v-min, $v-max);
			line-height: 140%;
			display: flex;
			align-items: center;
			
			letter-spacing: -0.015em;
			text-transform: uppercase;

			color: #FF5050;
		}
	}
	.form__header {
				display: flex;
				flex-direction: column;
				align-items: stretch;
				width: 80%;
				max-height: 321px;
				max-width: 422px;
				margin-bottom: 2rem;
				background: #E1E1E1;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				border-radius: 8px;
				padding: 13px 34px;
				color: #808080;
				font-weight: normal;
				font-size: d-font(10, 17, $v-min, $v-max);
				line-height: 120%;
				letter-spacing: -0.015em;
				text-transform: uppercase;
				input {
				   // box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
				   height: d-font(25, 42, $v-min, $v-max);
				   margin: 0;
				   padding: .375rem .75rem;
				   margin-bottom: px-to-rem(10px);
				   width: 100%;
				   // border-radius: 0 !important;
				}
				.form-control {
					background-color: white;
					border-radius: 7px;
					border: none;
				}
				
				p {
					font-style: normal;
					font-weight: 500;
					font-size: d-font(10, 17, $v-min, $v-max);
					line-height: d-font(10, 17, $v-min, $v-max);
					display: flex;
					align-items: center;
					justify-content: center;
					letter-spacing: -0.015em;
					text-transform: uppercase;
					color: #808080;
					margin-top: 0;
					margin-bottom: px-to-rem(8.85px);
				}
				button {
					min-width: 100%;
					font-size: d-font(14, 17, $v-min, $v-max);
				}
				p:last-child{
				   margin: 0.5em 0;
				   font-size: 12px;
				   line-height: 12px;
				   text-transform: none;
				   width: 100%;
				   text-align: center;
				}
				.btn {
				   color: white;
				   background: #A52A9C;
				   border-radius: 7px;
				   height: 40px;
				   width: 80%;
				   margin: 0;
				   margin-top: px-to-rem(5px);
				   margin-bottom: px-to-rem(20px);
				   text-transform: none;
				   padding: 0;

				}
			}
	.footer__block_6 {
		display: flex;
		flex-direction: row;
		align-items: center;
		padding: d-font(20,60, $v-min,$v-max) 0;
		a {
			cursor: pointer;
			text-decoration: none;
		}
		p {
			font-style: normal;
			font-weight: normal;
			font-size: d-font(6,14, $v-min,$v-max);
			line-height: 110%;
			letter-spacing: -0.015em;
			text-transform: uppercase;
			color: #000000;
		}
		.center__block_6 {
			width: d-img(110,395, $v-min, $v-max);
			margin-left: d-font(13,128, $v-min,$v-max);
			margin-right: d-font(13,128, $v-min,$v-max);
			img {
				width: 100%;
			}
		}
		.left__block_6 {
			text-align: right;
		}
		.right__block_6 {
			text-align: left;
		}
	}
}
@media screen and (min-width: 1000px) {
	.content__block_6 {
		padding-top: 3%;
		display: flex;
		flex-direction: row-reverse !important;
		align-items: center;
		justify-content: center;
		.title__block_6 {
			text-align: left;
			max-width: 825px;
			width: 50%;
			margin: 0;
		}
		.form__header {
			margin: 0;
		}
	}
}