.block__3 {
	padding-top: 34px;
   background-image: url('../img/bg__block_3.png');
   background-repeat: no-repeat;
   background-position: center right;
   background-size: cover;
   background-attachment:fixed;
  .title__block_3 {
  	display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 34px;
  		h3 {
  			width: 80%;
        font-style: normal;
        font-weight: bold;
        font-size: d-font(18, 48, $v-min, $v-max);
        line-height: 110%;
        text-align: center;
        text-transform: uppercase;
        color: white;
  		}
  }
  
  .description__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    .content__block_3 {
      width: 80%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .flex__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        .item__content {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 100px;
          .image_item {
                        width: 140px;
                        img {
                          width: 100%;
                        }
                      }

          .description__item {
            display: flex;
            align-items: center;
            text-align: center;
            
            
            p {
              width: d-font(220, 380, $v-min, $v-max);
              font-style: normal;
              font-weight: normal;
              font-size: d-font(18, 24, $v-min, $v-max);
              line-height: 111%;
              letter-spacing: -0.015em;
              color: #FFFFFF;
              margin-top: px-to-rem(15px);
              margin-bottom: px-to-rem(15px);
            }
          }
        }
      }
    }
    
  }
}
@media screen and (min-width: 534px) { 
  .block__3 {
    background-image: url('../img/background-block-3.png');
   background-repeat: no-repeat;
   background-attachment: center;
   background-size: cover;
  }
    .item__content {
          display: flex;
          flex-direction: row !important;
          align-items: center;
          .image_item {
              width: 140px;
              img {
                width: 100%;

              }
            }

          .description__item {
            display: flex;
            align-items: center;
            text-align: left !important;
            margin-left: px-to-rem(57px);
            
            p {
              width: d-font(220, 380, $v-min, $v-max);
              font-style: normal;
              font-weight: normal;
              font-size: d-font(18, 24, $v-min, $v-max);
              line-height: 111%;
              letter-spacing: -0.015em;
              color: white !important;
              margin-top: px-to-rem(15px);
              margin-bottom: px-to-rem(15px);
            }
          }
        }
}
@media screen and (min-width: 1280px) {
  
  .description__block {
    .content__block_3 {
      flex-direction: row !important;
      align-items: flex-start !important;
      justify-content: space-around;
      margin-bottom: px-to-rem(50px);
      .flex__content {
        justify-content: space-between !important;
        .item__content {
          min-height: 160px;
          margin: 0 0 50px 0 !important;

          img {
            height: 100% !important;
            width: auto !important;
          }
        }
      }
    }
  }
}