.image__last__block {
	width: 100%;
	height: d-font(237, 727, $v-min, $v-max);
	background-image: url('../img/childrens.png');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}
@media screen and (min-width: 814px) {
	.image__last__block {
		width: 100%;
		height: d-font(237, 727, $v-min, $v-max);
		background-image: url('../img/childrens_2.png');
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}
}
.last__block {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	background: #7A75B0;
	.title_last_block {
		display: flex;
		flex-direction: column;
		align-items: center;
		.title__block {
			text-align: center;
			width: d-font(280, 558, $v-min, $v-max);;
			
			h3 {
				font-style: normal;
				font-weight: bold;
				font-size: d-font(18, 36, $v-min, $v-max);
				line-height: 140%;
				display: flex;
				align-items: center;
				
				letter-spacing: -0.015em;
				text-transform: uppercase;

				color: white;
			}
		}
		.check__list {
			display: flex;
			flex-direction: row;
			justify-content: center;
			width: 100%;
			align-items: center;
			
			.img__block_5 {
				width: d-font(80, 152, $v-min, $v-max);
				height: d-font(69, 202, $v-min, $v-max);
				background-image: url('../../img/present.png');
				background-repeat: no-repeat;
				background-size: contain;
				background-position: center;
			}
			.description__block_5{
				width: d-font(200, 524, $v-min, $v-max);
				
				margin-left: 5%;
				p {
					font-style: normal;
					font-weight: normal;
					font-size: d-font(10, 24, $v-min, $v-max);
					line-height: 130%;
					display: flex;
					align-items: center;
					letter-spacing: -0.015em;
					text-transform: uppercase;
					margin: 0;
					color: #FFFFFF;
				}
			}
		}
	}
	.form__last_block {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: d-font(16, 40, $v-min, $v-max);
		.form__header {
				display: flex;
				flex-direction: column;
				align-items: stretch;
				width: d-font(288, 422, $v-min, $v-max);
				max-height: 321px;
				max-width: 422px;
				margin-bottom: 2rem;
				background: #E1E1E1;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				border-radius: 8px;
				padding: 13px 34px;
				color: #808080;
				font-weight: normal;
				font-size: d-font(10, 17, $v-min, $v-max);
				line-height: 120%;
				letter-spacing: -0.015em;
				text-transform: uppercase;
				input {
				   // box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
				   height: d-font(25, 42, $v-min, $v-max);
				   margin: 0;
				   padding: .375rem .75rem;
				   margin-bottom: px-to-rem(10px);
				   width: 100%;
				   // border-radius: 0 !important;
				}
				.form-control {
					background-color: white;
					border-radius: 7px;
					border: none;
				}
				
				p {
					font-style: normal;
					font-weight: 500;
					font-size: d-font(10, 17, $v-min, $v-max);
					line-height: d-font(10, 17, $v-min, $v-max);
					display: flex;
					align-items: center;
					justify-content: center;
					letter-spacing: -0.015em;
					text-transform: uppercase;
					color: #808080;
					margin-top: 0;
					margin-bottom: px-to-rem(8.85px);
				}
				button {
					min-width: 100%;
					font-size: d-font(14, 17, $v-min, $v-max);
				}
				p:last-child{
				   margin: 0.5em 0;
				   font-size: 12px;
				   line-height: 12px;
				   text-transform: none;
				   width: 100%;
				   text-align: center;
				}
				.btn {
				   color: white;
				   background: #A52A9C;
				   border-radius: 7px;
				   height: 40px;
				   width: 80%;
				   margin: 0;
				   margin-top: px-to-rem(5px);
				   margin-bottom: px-to-rem(20px);
				   text-transform: none;
				   padding: 0;

				}
			}
	}
}
@media screen and (min-width: 900px) {
	.last__block {
		display: flex;
		flex-direction: row-reverse;
		justify-content: center;

	}
	.title_last_block {
		margin-left: d-font(40, 72, $v-min, $v-max);
		align-items: flex-start !important;
		justify-content: space-around;
		margin-bottom: 1.8rem !important;
		padding-top: d-font(16, 40, $v-min, $v-max);
		h3 {
			margin: 0;
		}
	}
	.title__block {
		text-align: left !important;
	}
	.check__list {
		display: flex;
		flex-direction: row-reverse !important;
		justify-content: space-between !important;
		.img__block_5 {
			margin-left: d-font(5, 28, $v-min, $v-max);
			background-position: center;
			background-image: url('../../img/present_2.png') !important;
		}
		.description__block_5 {
			margin-left: 0 !important;
		}
	}
}