.block__4 {
	
	background: linear-gradient(180.15deg, rgba(196, 191, 191, 0) 49.28%, #C7C6C6 91.91%, rgba(199, 198, 198, 0.33) 99.73%);
	padding-bottom: 30px;
	.title {
		display: flex;
    	flex-direction: column;
   	align-items: center;
    	justify-content: center;
   	width: 100%;
    	margin-bottom: 34px;
  		h3 {
  			width: 80%;
        font-style: normal;
        font-weight: bold;
        font-size: d-font(18, 48, $v-min, $v-max);
        line-height: 110%;
        text-align: center;
        text-transform: uppercase;
        color: #4C0978;
  		}
	}
	.content__block_4 {
		display: flex;
		flex-direction: column;
		align-items: center;
		// justify-content: space-around;
		.first {
			width: d-font(200, 586, $v-min, $v-max);
			margin-bottom: px-to-rem(20px);
			img {
				width: 100%;
			}
		}
		.second {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: space-around;
			.item {
				display: flex;
				flex-direction: row;
				align-items: flex-start;
				padding-bottom: px-to-rem(10px);
				img{
				     width: 100%;
				    }
				.image-star img{
				        width: d-font(17, 30, $v-min, $v-max);
				        margin-right: 2rem;
				        
				    }
				.description{
					display: flex;
					flex-direction: row;
					align-items: flex-start;
				   font-size: d-font(12, 18, $v-min, $v-max);
				   line-height: 140%;
				 }

			}
		} 

	}
}

@media screen and (min-width: 1200px) {
	.block__4 {
		.content__block_4 {
			display: flex;
			flex-direction: row !important;
			justify-content: space-around;
			align-items: stretch;
			.first {
				width: 100% !important;
			}

			.image-star img{
				margin-left: 2rem;
			}
		}
	}
		
}